.app {
  text-align: center;
}
.app-header {
  background-color: #333;
  color: white;
  padding: 10px;
  /* height: 40px; */
  margin-bottom: 2rem;
}
.app-body {
  height: calc(100vh - 40px);
}
.grid-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.grid-item {
  background-color: #1a1a1a;
  padding: 20px;
  font-size: 30px;
  text-align: center;
  margin: 0.5rem;
  height: 100px;
  width: 100px;
  border-radius: 10px;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.container {
  display: grid;
  grid-template-columns: auto auto auto;
}

.card {
  width: 100px;
  height: 100px;
  background-color: #1a1a1a;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
